.bg-gradient {
  position: relative;
  &::before {
    position: absolute;
    content: '';
    width: 0px;
    height: 100%;
    top: 0;
    left: 50%;
    border-left: 1px dashed #151515;
    transform: translateX(-50%);
  }
}
.join-input {
  .lucid-input-suffix,
  .lucid-input {
    font-size: 40px !important;
    font-weight: 700;
    text-align: right;
  }
  .lucid-input-suffix {
    color: #b3f5d9;
  }
}

/* CSS */
.button-53 {
  background-color: #087f31;
  border: 0 solid #087f31;
  box-sizing: border-box;
  color: #000000;
  display: flex;
  font-family: ui-sans-serif, system-ui, -apple-system, system-ui, 'Segoe UI',
    Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif,
    'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-size: 1rem;
  font-weight: 700;
  justify-content: center;
  line-height: 1.75rem;
  padding: 0.75rem 1.65rem;
  position: relative;
  text-align: center;
  text-decoration: none #000000 solid;
  text-decoration-thickness: auto;
  width: 100%;
  max-width: 460px;
  position: relative;
  cursor: pointer;
  transform: rotate(-2deg);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-53:focus {
  outline: 0;
}

.button-53:after {
  content: '';
  position: absolute;
  border: 1px solid #000000;
  bottom: 4px;
  left: 4px;
  width: calc(100% - 1px);
  height: calc(100% - 1px);
}

.button-53:hover:after {
  bottom: 2px;
  left: 2px;
}

@media (min-width: 768px) {
  .button-53 {
    padding: 0.75rem 3rem;
    font-size: 1.25rem;
  }
}

@ant-prefix: lucid;